// 配置路由器
import VueRouter from "vue-router";
import FirstRoute from '../pages/FirstRoute';
import SecondRoute from '../pages/SecondRoute';
import ThirdRoute from '../pages/ThirdRoute';
import FourthRoute from '../pages/FourthRoute';
import FifthRoute from '../pages/FifthRoute';

export default new VueRouter({
    routes: [
        {
            path: '/First',
            component: FirstRoute
        },
        {
            path: '/Second',
            component: SecondRoute
        },
        {
            path: '/Third',
            component: ThirdRoute
        },
        {
            path: '/Fourth',
            component: FourthRoute
        },
        {
            path: '/Fifth',
            component: FifthRoute
        },

    ]
})